import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { WebShareApi } from 'utils/webApi';

const Share = () => {
  const [imageUrl, setImageUrl] = useState();
  const [rules, setRules] = useState([]);
  const [notes, setNotes] = useState([]);
	const {t} = useTranslation()
  useEffect(() => {
    WebShareApi().then((res) => {
      setImageUrl(res.data.imageUrl);
      setRules(res.data.rules);
      setNotes(res.data.notes);
    });
  }, []);

	const handleClick = () => {
		
	}
  return (
    <div className="flex flex-col h-screen">
      <div
        className="w-screen h-2/4 flex flex-col justify-end items-center "
        style={{
					backgroundImage: `url(${process.env.REACT_APP_SERVER + imageUrl})`,
					backgroundRepeat: 'no-repeat',
					backgroundSize: "100% 100% "
        }}
      >
          <button className="rounded-2xl bg-green-600 mb-8 w-48 h-10 font-bold text-sm  " onClick={() => handleClick()}>
            {t('share_now')}
          </button>
					<div className='flex flex-col w-screen item-start m-4 pl-4 text-white gap-2 '>
          {
						notes.map((item, index) => (
            <div key={index} className="flex gap-2 ">
              <p className="rounded-full w-6 h-6 leading-6 bg-green-600 text-center">
                {index + 1}
              </p>
              <p className='w-10/12 tracking-wider leading-6 '>{item.rule}</p>
            </div>
					))
					}
					</div>
      </div>
      <div className="">
        <div className="p-2 w-24  m-auto border-solid border-b-2 border-b-red-500  text-center">
          {t('share_rule')}
        </div>
        <div className="flex flex-col p-4 gap-4">
          {
						rules.map((item, index) => (
            <div key={index} className="flex gap-2 ">
              <p className="rounded-full w-6 h-6 leading-6 bg-gray-300 text-center">
                {index + 1}
              </p>
              <p className='w-10/12 tracking-wider leading-6 '>{item.rule}</p>
            </div>
					))
					}
        </div>
      </div>
    </div>
  );
};

export default Share;
